.unveil {
  background-color: $color-black;
  height: 100vh;
  width: 100%;
  animation: hide 1s ease forwards 4s, remove 1s ease forwards 4.5s;
  z-index: 100;

  @keyframes hide {
    to {
      opacity: 0;
    }
  }

  @keyframes remove {
    to {
      height: 0;
    }
  }
}

#logo {
  max-height: 250px;
  margin-left: -150px;
  animation: fill-color 1.1s ease forwards 3.1s;

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(0, -50%);
}

#logo path:nth-child(1) {
  stroke-dasharray: 956;
  stroke-dashoffset: 956;

  animation: fill-borders 1.5s ease forwards;
}

#logo path:nth-child(2) {
  stroke-dasharray: 981;
  stroke-dashoffset: 981;
  animation: fill-borders 1.5s ease forwards 0.3s;
}

#logo path:nth-child(3) {
  stroke-dasharray: 481;
  stroke-dashoffset: 481;
  animation: fill-borders 1.5s ease forwards 0.6s;
}

#logo path:nth-child(4) {
  stroke-dasharray: 941;
  stroke-dashoffset: 941;
  animation: fill-borders 1.5s ease forwards 0.9s;
}

#logo path:nth-child(5) {
  stroke-dasharray: 631;
  stroke-dashoffset: 631;
  animation: fill-borders 1.5s ease forwards 1.2s;
}

#logo path:nth-child(6) {
  stroke-dasharray: 631;
  stroke-dashoffset: 631;
  animation: fill-borders 1.5s ease forwards 1.5s;
}

#logo path:nth-child(7) {
  stroke-dasharray: 941;
  stroke-dashoffset: 941;
  animation: fill-borders 1.5s ease forwards 1.8s;
}

#logo path:nth-child(8) {
  stroke-dasharray: 617;
  stroke-dashoffset: 617;
  animation: fill-borders 1.5s ease forwards 2.1s;
}

@keyframes fill-borders {
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes fill-color {
  from {
    fill: transparent;
  }
  to {
    fill: white;
  }
}
